.prod-page {
	&__top {
		@include clr;
		margin-bottom: 55px;
		@include md {
			margin-bottom: 40px; }
		@include sm {
			margin-bottom: 25px; } }
	&__slider-side, &__desrc-side, &__title {
		@include sm {
			float: none;

			padding: 0; } }
	&__slider-side {
		width: percentage(360 / 1140);
		float: left;
		padding-top: 8px;
		@include sm {
			width: 258px;
			margin: 0 auto 20px; } }
	&__desrc-side, &__title {
		width: percentage(652 / 1140);
		float: right;
		@include md {
			width: 65%; }
		@include sm {
			width: 100%; } }
	&__help-block {
		font-size: 24px;
		@include m {
			font-size: 18px; }
		p {
			margin-bottom: 5px; }
		a {
			font-size: 30px;
			color: $blue;
			@include rob-m;
			@include m {
				font-size: 20px; } } }
	&__title {
		text-align: left;
		margin-bottom: 5px;
		@include sm {
			text-align: center;
			margin-bottom: 20px; } }
	&__two-col {
		margin-bottom: 80px;
		@include md {
			margin-bottom: 50px; }
		@include sm {
			margin-bottom: 25px; } } }
.prod-main-slider {
	margin-bottom: 19px;
	font-size: 0;
	&__slide {
		height: 283px;
		img {
			object-fit: cover;
			width: 100%;
			height: 100%; } } }
.prod-nav-slider {
	font-size: 0;
	margin-bottom: 60px;
	@include m {
		margin-bottom: 40px; }
	@include sm {
		margin-bottom: 25px; }
	&__slide {
		width: 67px;
		height: 64px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	.slick-slide {
		@include trans;
		width: 67px!important;
		margin: 0 15px;
		cursor: pointer; } }
.prod-rating {
	margin-bottom: 50px;
	font-size: 0;
	@include md {
		margin-bottom: 20px; }
	&__count, &__text {
		display: inline-block;
		vertical-align: top; }
	&__count {
		@include rob;
		font-size: 18px;
		margin-right: 71px;
		padding-top: 2px; }
	&__text {
		@include robc-b;
		font-size: 24px;
		color: $green;
		padding-bottom: 2px; }
	.star-ratings {
		margin-right: 10px; } }
.prod-form {
	max-width: 555px;
	&__text {
		margin-bottom: 17px;
		@include md {
			margin-bottom: 10px; } }
	&__line {
		font-size: 0;
		padding-top: 6px;
		@include sm {
			text-align: center; }
		&--btn {
			text-align: right;
			@include m {
				max-width: 449px; }
			@include sm {
				max-width: 100%;

				padding-top: 20px; } }

		.prod-form__block {
			&:last-of-type {
				margin-right: 0;
				@include sm {
					margin-right: 15px; } } } }
	&__block {
		width: 164px;
		display: inline-block;
		vertical-align: top;
		margin-right: 30px;
		margin-bottom: 33px;
		@include m {
			width: 140px;
			margin-right: 15px; }
		@include xs {
			margin: 0 15px 15px; }

		label {
			display: block;
			height: 55px;
			background-color: $white;
			border: 1px solid $blue-d;
			@include trans;
			color: $blue;
			@include rob-m;
			text-align: center;
			cursor: pointer;
			@include vertical;
			&:hover {
				background: $blue-d;
				color: $white; } }
		span {
			display: inline-block;
			vertical-align: middle;
			font-size: 20px;
			@include m {
				font-size: 16px; } }
		input {
			display: none;
			&:checked + label {
				background: $blue-d;
				color: $white; } } }
	&__price {
		min-height: 75px;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		padding: 10px 5px;
		border-left: 1px solid $blue;
		border-right: 1px solid $blue;
		border-bottom: 1px solid $blue;
		color: $blue;
		font-size: 20px;
		@include m {
			font-size: 14px; }
		p {
			color: $green; } } }
.prod-lists {
	padding-top: 66px;
	@include md {
		padding-top: 40px; }
	@include sm {
		padding-top: 25px; }
	.list {
		margin-bottom: 58px;
		@include sm {
			margin-bottom: 25px; } } }
.prod-questions {
	margin-bottom: 51px;
	@include m {
		margin-bottom: 35px; }
	&__inner {
		padding-left: 98px;
		@include md {
			padding-left: 0; } } }
.prod-question {
	margin-bottom: 28px;
	@include m {
		margin-bottom: 15px; }
	&__inner {
		@include clr; }
	&__title {
		margin-bottom: 28px;
		padding-top: 10px;
		@include sm {
			margin-bottom: 20px; } }
	&__text {
		p {
			margin-bottom: 15px; }
		br {
			@include sm {
				display: none; } } }
	img {
		max-width: 262px;
		max-height: 328px;
		height: auto;
		float: right;
		margin-bottom: 10px;
		margin-left: 30px;
		@include sm {
			display: none; } }
	&--img-left {
		img {
			float: left;
			margin-left: 0;
			margin-right: 30px; } } }
