.products {
	.line-block {
		margin-top: 91px;
		margin-bottom: 3px;
		@include md {
			margin-top: 45px; }
		&__inner {
			max-width: 955px; }
		& + .prod-item {
			border-top-color: transparent; } } }
.prod-item {
	position: relative;
	&:not(.prod-item--small, .prod-item--compare) {
		border-top: 2px solid whitesmoke;
		margin-bottom: 43px;
		.prod-item__text {
			margin-bottom: 22px; }
		.prod-item__img {
			margin-bottom: 49px;
			@include sm {
				height: auto; }
			&:after {
				width: 31px;
				bottom: -17px; } }

		.prod-item__inner {
			@include sm {
				display: flex;
				flex-wrap: wrap; } }
		.prod-item__btn {
			@include sm {
				display: none; }
			&--mob {
				display: none;
				@include sm {
					display: block;
					max-width: 200px;
					margin: 25px auto 0; } } } }
	&--compare {
		width: 165px;
		text-align: center;
		border-top: 2px solid $blue;
		margin-right: 30px;
		display: inline-block;
		vertical-align: top;
		white-space: normal;
		@include lg {
			width: 120px; }
		@include m {
			margin-right: 10px; }
		&:last-of-type {
			margin-right: 0; }
		.prod-item__img {
			height: 152px;
			margin-bottom: 22px;
			@include lg {
				height: 120px; }
			@include sm {
				height: 90px; }
			&:after {
				width: 30px;
				bottom: -8px; }
			img {
				max-width: 150px;
				max-height: 112px;
				@include lg {
					max-width: 100%; } } }
		.prod-item__title {
			font-size: 20px;
			@include rob-l;
			margin-bottom: 0;
			@include lg {
				font-size: 16px; } }
		.prod-item__price {
			margin-bottom: 0;
			color: $blue-d;
			@include robc-b;
			font-size: 36px;
			@include lg {
				font-size: 20px; } }
		.prod-item__btn {
			display: block;
			width: 100%;
			min-width: 100%;
			font-size: 20px;
			padding: 8px 12px; } }

	&--popular {
		.prod-item__price {
			color: $green; }
		.prod-item__compare-value {
			svg {
				fill: $green; } } }
	&--small {
		background: $white;
		border-top: 3px solid $blue;
		border-bottom: 3px solid $blue;
		box-shadow: 0 0 8px rgba(0, 0, 0, 0);
		flex-basis: 359px;
		flex-grow: 0;
		flex-shrink: 1;
		margin-right: 30px;
		margin-bottom: 56px;
		text-align: center;
		padding-bottom: 100px;
		min-height: 515px;
		@include trans;

		&:nth-of-type(3n) {
			margin-right: 0; }
		@include rmin(1024) {
			&:hover {
				box-shadow: 0 0 8px rgba(0, 0, 0, 0.14); } }
		@include lg {
			flex-basis: 300px;
			min-height: 480px; }
		@include md {
			flex-basis: 30%;
			min-height: 400px; }
		@include sm {
			min-height: auto;
			padding-bottom: 70px;
			max-width: 280px;
			margin-left: auto;
			margin-bottom: 25px;
			margin-right: auto;
			&:nth-of-type(3n) {
				margin-right: auto; } }
		.prod-item__title {
			margin-bottom: 17px;
			padding: 0 5px; }
		.prod-item__text {
			@include rob-t;
			flex: 1;
			font-size: 24px;
			padding: 0 5px;
			@include md {
				font-size: 16px; } }
		.prod-item__btn {
			position: absolute;
			bottom: 39px;
			left: 50%;
			transform: translateX(-50%);
			@include m {
				min-width: auto;
				width: 90%;
				margin: 0 auto; }
			@include sm {
				bottom: 15px; } }
		.prod-item__img {
			@include lg {
				height: 200px;
				margin-bottom: 15px;
				img {
					max-height: 200px; } }
			@include m {
				height: 165px;
				img {
					max-height: 165px; } }
			@include ms {
				height: 155px;
				img {
					max-height: 155px; } } } }
	&__img {
		height: 241px;
		position: relative;
		margin: 0 auto 28px;
		&--spec-position {
			img {
				position: relative;
				top: 40px;
				@include sm {
					top: 0; } } }
		&:after {
			content: '';
			position: absolute;
			width: 41px;
			height: 4px;
			background-color: $blue;
			left: 50%;
			transform: translateX(-50%);
			bottom: -2px;
			z-index: -1; }
		img {
			max-width: 100%;
			height: auto;
			max-height: 241px; } }
	&__pupular {
		font-size: 13px;
		color: $white;
		@include robc-b;
		text-align: center;
		background: $green;
		left: 0;
		top: 0;
		z-index: 1;
		position: absolute;
		padding: 4px;
		width: 57px; }
	&__inner {
		@include clr; }
	&__left {
		float: left;
		width: 360px;
		text-align: center;
		@include sm {
			float: none;
			order: 1;
			margin: 0 auto; } }
	&__right {
		padding-right: 95px;
		float: right;
		width: calc(100% - 390px);
		@include sm {
			order: 2;
			flex-basis: 100%; }
		&--pt {
			padding-top: 44px;
			@include sm {
				order: 0;
				text-align: center;
				padding-top: 15px; }
			.title-line {
				@include sm {
					margin-bottom: 10px;
					&:after {
						display: none; } } } }
		@include md {
			padding-right: 40px; }
		@include m {
			padding-right: 0; }
		@include sm {
			float: none;
			width: 100%; } }


	&__title {
		@include rob;
		color: $blue;
		font-size: 28px;
		margin-bottom: 7px;
		@include md {
			font-size: 18px; } }
	&__text {
		// a
		// 	color: $blue
 }		// 	text-decoration: underline
	&__price {
		color: $blue;
		font-size: 24px;
		@include rob-m;
		margin-bottom: 18px; }
	&__compare-value {
		// padding-top: 10px
		i {
			display: inline-block;
			vertical-align: top;
			font-size: 0;
			margin-top: 10px;
			@include lg {
				margin-top: 0px; } }
		svg {
			fill: $blue;
			font-size: 22px; } } }
.prod-list {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin-bottom: 26px;
	@include sm {
		display: block;
		margin-bottom: 0; } }
