.user-section {
	margin-bottom: 71px;
	@include md {
		margin-bottom: 50px; }

	&--comments {
		margin-top: 36px;
		margin-bottom: 55px;
		@include sm {
			margin-bottom: 25px; }
		.l {
			border-top: 2px solid  whitesmoke;
			padding-top: 32px;
			@include md {
				padding-top: 20px; }
			@include sm {
				padding-top: 10px; } }
		.user-section__inner {
			padding-left: 0; }
		.user-block {
			margin-bottom: 40px;
			@include sm {
				margin-bottom: 25px; } } }
	&__inner {
		padding-left: 98px;
		position: relative;
		@include md {
			padding-left: 0; } }
	&__btn {
		position: absolute;
		right: 0;
		top: 0;
		@include md {
			top: -5px; }
		@include sm {
			position: static;
			max-width: 200px;
			display: block;
			margin: 25px auto 0; } }
	.title-lg {
		padding-right: 98px;
		margin-bottom: 37px;
		@include sm {
			padding-right: 0; } }
	.title-line {
		padding-top: 10px;
		margin-bottom: 51px;
		@include md {
			margin-bottom: 30px; } } }


.user-block {
	margin-bottom: 51px;
	@include md {
		margin-bottom: 40px; }
	@include sm {
		margin-bottom: 25px; }
	&--review {
		.user-block__text, .user-block__date {
			color: $blue-d; } }
	&__rate-line {
		font-size: 0;
		margin-bottom: 7px; }
	&__spec-title {
		display: inline-block;
		vertical-align: top;
		margin-right: 70px;
		@include sm {
			margin-right: 0;
			margin-bottom: 10px; } }
	&__name {
		font-size: 24px;
		margin-bottom: 12px;
		@include md {
			font-size: 20px; }
		@include sm {
			font-size: 16px; } }
	&__text, &__date {}
	&__text {
		margin-bottom: 10px; }
	.star-ratings {
		margin-top: 3px;
		@include sm {
			display: block;
			margin-bottom: 10px; } } }
