.hero {
	padding-top: 152px;
	background-repeat: no-repeat;
	background-position: 50% 100%;
	background-size: cover;
	overflow: hidden;
	border-bottom: 5px solid $blue;
	margin-bottom: 85px;
	@include md {
		padding-top: 120px;
		margin-bottom: 60px; }
	@include sm {
		padding-top: 150px;
		margin-bottom: 40px; }
	&__inner {
		text-align: center;
		max-width: 585px;
		position: relative;
		@include md {
			max-width: 100%; } }
	&__title {
		margin-bottom: 75px;
		@include md {
			margin-bottom: 60px; }
		@include sm {
			margin-bottom: 20px; } }
	&__btn {
		margin-bottom: 83px;
		@include sm {
			margin-bottom: 40px; } }
	&__text {
		@include rob-b;
		color: $blue-d;
		font-size: 30px;
		border: 2px dashed $blue;
		padding: 14px 57px 14px 25px;
		border-radius: 18px;
		margin-left: 25px;
		max-width: 550px;
		@include lg {
			padding: 14px 25px 14px 25px; }
		@include md {

			margin-left: auto;
			margin-right: auto; }
		@include sm {
			font-size: 16px;
			max-width: 350px;
			padding: 14px; }
		span {
			display: block;
			margin-top: 12px;
			color: $green;
			font-size: 36px;
			@include sm {
				font-size: 20px; } } }
	&__img {
		position: absolute;
		z-index: 1;
		bottom: -64px;
		right: -183px;
		@include lg {
			max-width: 500px;
			height: auto;
			right: -62px; }
		@include md {
			display: none; } }
	.l {
		padding-bottom: 88px;
		@include sm {
			padding-bottom: 50px; } } }
