.btn-next, .btn-prev {
	cursor: pointer;
	display: inline-block;
	vertical-align: top;
	line-height: 1;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	svg {
		height: 36px;
		fill: $blue;
		@include trans;
		&:hover {
			fill: $blue-d; } } }
.btn-next {
	right: 2px; }
.btn-prev {
	left: 2px; }
.slider {
	max-width: 1050px;
	padding: 0 50px;
	margin: 0 auto;
	@include m {
		padding: 0 30px; }
	&__slide {}
	&__inner {
		@include clr;
		@include sm {
			display: flex;
			flex-wrap: wrap; } }
	&__img,	&__title, &__text {
		@include sm {
			float: none;
			width: 100%; } }
	&__img {
		width: 262px;
		float: left;
		@include sm {
			order: 1; }

		img {
			max-width: 100%;
			max-height: 270px;
			height: auto;
			display: block;
			margin-left: auto;
			margin-right: auto; } }
	&__title, &__text {
		float: right;
		width: calc(100% - 292px);
		padding-top: 18px; }
	&__text {
		@include md {
			font-size: 18px; }
		@include sm {
			font-size: 14px;
			order: 2; } }
	&__title {
		padding-top: 18px;
		color: $blue-d;
		font-size: 24px;
		@include rob-m;
		margin-bottom: 44px;
		@include sm {
			order: 0;
			padding-top: 0;
			text-align: center;
			margin-bottom: 30px; }
		span {
			display: inline-block;
			vertical-align: top;
			position: relative;
			&:before {
				content: '';
				position: absolute;
				width: 210px;
				height: 2px;
				background: $green;
				bottom: -9px;
				right: -24px;
				@include sm {
					right: 0;
					width: auto;
					left: 0; } } } } }
