.compare {
	margin-bottom: 70px;
	&__nav {
		display: flex;
		align-items: center;
		justify-content: center;
		padding-bottom: 18px;
		border-bottom: 1px solid $green;
		margin-bottom: 42px;
		@include sm {
			display: block; }
		a, span {
			margin:  0 30px;
			@include sm {
				margin: 0; } }
		span {
			color: $blue;
			@include sm {
				display: block;
				margin-bottom: 10px; } }
		a {
			font-size: 24px;
			@include rob-m;
			color: $blue-d;
			@include trans;
			position: relative;
			@include sm {
				font-size: 18px;
				margin-right: 10px; }
			&:after {
				content: '';
				position: absolute;
				width: 100%;
				height: 2px;
				background: $green;
				opacity: 0;
				@include trans;
				left: 0;
				bottom: -5px; }
			&.is-active {
				color: $green;
				&:after {
					opacity: 1; } }
			@include rmin(1024) {
				&:hover {
					&:after {
						opacity: 1; } } } } }
	&__inner {
		@include clr;
 }		// display: flex
	// &__options-col, &__content
	// 	flex-grow: 0
	// 	flex-shrink: 0
	&__options-col {
		float: left;
		width: 180px;
		// flex-basis: 180px
		padding-right: 15px;
		@include lg {
			width: 165px; }
		@include sm {
			padding-right: 10px;
			width: 110px;
			font-size: 12px; } }
	&__option {
		color: $blue-d; }

	&__content {
		float: right;
		// width: 1140 - 180 + px
		width: calc(100% - 180px);
		// flex-basis: 960px

		padding-left: 13px;
		overflow-x: auto;
		@include lg {
			padding-left: 0;
			width: calc(100% - 165px); }
		@include sm {
			width: calc(100% - 110px); } }
	&__list {
		// width: 947px
		overflow-x: auto;
		font-size: 0;
		white-space: nowrap; } }
.compare__option, .prod-item__compare-value {
	margin-bottom: 16px;
	min-height: 76px;
	@include lg {
		min-height: 34px;
		margin-bottom: 10px; }
	&--big {
		height: 239px;
		margin-bottom: 0;
		@include lg {
			height: 200px; }
		@include sm {
			height: 160px; } } }

.compare__option {
	&--big {
		padding-top: 40px;
		font-size: 24px;
		@include md {
			font-size: 18px; }
		@include sm {
			font-size: 14px; } } }
.switch-value {
	display: none;
	&.is-active {
		display: block; } }
.video-section {
	padding: 68px 0;
	@include md {
		padding: 40px 0; }
	@include sm {
		padding: 25px 0; }
	&__text {
		margin-bottom: 25px;
		text-align: center; }
	&__video-wrapper {
		max-width: 943px;
		margin-left: auto;
		margin-right: auto;
		height: 530px;
		@include m {
			max-width: 600px;
			height: 350px; }
		@include sm {
			max-width: 400px;
			height: 220px; }
		iframe {
			display: block;
			width: 100%;
			height: 100%; }
		// position: relative
		// padding-bottom: 56.25%
		// overflow: hidden
		// frame, object, embed
		// 	width: 100%
		// 	height: 100%
		// 	display: block
		// 	position: absolute
		// 	top: 0
 } }		// 	left: 0

