.content-block {
	padding-top: 144px;
	@include md {
		padding-top: 120px; }
	@include sm {
		padding-top: 150px; } }
.article {
	&__cols {
		@include clr;
		margin-bottom: 23px;
		@include sm {
			margin-bottom: 10px; }
		.title-line {
			text-align: left;
			margin-bottom: 48px;
			@include md {
				font-size: 25px; }
			@include sm {
				font-size: 20px; } }
		&--mb-big {
			margin-bottom: 70px;
			@include md {
				margin-bottom: 50px; }
			@include sm {
				margin-bottom: 30px; } } }
	&__left, &__right {
		@include sm {
			float: none;
			width: 100%;
			margin-bottom: 20px; } }
	&__left {
		float: left;
		width: percentage(655 / 1140); }

	&__right {
		float: right;
		width: percentage(457 / 1140);
		text-align: right;
		font-size: 0;
		@include sm {
			text-align: center; }
		img {
			max-width: 100%;
			max-height: 700px;
			height: auto; } }
	a {
		color: $blue;
		position: relative;
		border-bottom: 1px solid $blue;
		@include trans;
		&:hover {
			border-color: transparent; } }
	ul {
		margin-bottom: 22px; }
	p {
		margin-bottom: 22px; }
	h2,h3,h4,h5,h6 {
		color: $blue-d; }
	h2 {
		@include robc-b;
		font-size: 36px;
		margin-bottom: 8px;
		@include md {
			font-size: 20px; }
		@include sm {
			font-size: 18px; } }
	h3 {
		font-size: 24px;
		@include rob-m;
		margin-bottom: 9px;
		@include md {
			font-size: 18px; }
		@include sm {
			font-size: 16px; } } }
.decor-text {
	padding-left: 19px;
	border-left: 1px solid $green; }
blockquote {
	@include rob-m;
	font-style: italic;
	font-size: 22px;
	color: $blue-d;
	padding: 40px 0 40px 40px;
	position: relative;
	max-width: 715px;
	margin: 0 auto 46px;
	@include md {
		font-size: 18px; }
	@include sm {
		font-size: 16px; }
	&:before, &:after {
		content: '';
		position: absolute;
		width: 67px;
		height: 2px;
		background: $blue;
		left: 45%;
		transform: translateX(-50%); }
	&:before {
		top: 7px; }
	&:after {
		bottom: 10px; }
	i {
		@include s(blockquote);
		display: block;
		position: absolute;

		left: 0px;
		top: 32px; } }
